.footer{
  &__wrapper{
    &-yellow{
      display: flex;
      align-items: flex-end;

      height: 425px;

      padding: 0 0 56px 0;

      background: url("../img/footer-yellow.svg") center top/cover no-repeat;
    }
  }
  &__logo{
    width: 295px;
    height: 84px;
  }
  &__row{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    &-block{
      &_right{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
      }
    }
  }
  &__block{
    & + &{
      margin: 24px 0 0 0;
    }
    &-mobile{
     display: none;
    }
  }
  &__text{
    font-family: 'Graphik LCG', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;

    color:#fff;
    &-right{
      text-align: right;
    }
  }
  &__link{
    text-decoration: underline;
    text-underline-position: under;
  }
}
@media screen and (max-width: 767px) {
  .footer{
    &__wrapper{
      &-yellow{
        height: 363px;
        background: url("../img/footer-yellow-m.svg") center top/cover no-repeat;
      }
    }
    &__logo{
      width: 116px;
      height: 33px;
    }
    &__text{
      font-size: 14px;
    }
    &__row{
      align-items: flex-start;
      &-block{
        width: 50%;
      }
    }
    &__block{
      &-desktop{
        display: none;
      }
      &-mobile{
        display: block;
      }
      & + &{
        margin: 16px 0 0 0;
      }

      &-logo + &{
        margin: 32px 0 0 0;
      }
    }
  }
}