.header__wrapper {
  background: #FFFFFF;
  box-shadow: 0px 4px 20px 2px rgba(0, 52, 153, 0.05);
}

.header__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 64px;
}

.header__block {
  display: flex;
  align-items: center;
}

.header__logo {
  width: 118px;
  height: 34px;
}

.header__profile {
  display: flex;
  align-items: center;
  margin: 0 0 0 115px;
}

.header__profile-block + .header__profile-block {
  margin-left: 32px;
}

.header__profile-block_burger {
  display: none;
}

.header-nav__list {
  display: flex;
  align-items: center;
}

.header-nav__list-mobile {
  display: none;
}

.header-nav__divider {
  display: none;
}

.header-nav__item + .header-nav__item {
  margin-left: 48px;
}

.header-nav__link {
  font-family: 'Graphik LCG', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
}

.header-nav__link.active {
  position: relative;
}

.header-nav__link.active::after {
  content: '';
  position: absolute;
  left: -2px;
  bottom: -3px;
  width: calc(100% + 4px);
  height: 2px;
  background: #6776FF;
}

.header-profile__wrapper {
  position: relative;
}

.header-profile__link {
  display: flex;
  align-items: center;
}

.header-profile__link::after {
  content: '';
  width: 16px;
  height: 16px;
  display: block;
  margin-left: 8px;
  background: url("../img/arrow-blue.svg") center center/contain no-repeat;
  transition: .3s rotate;
}

.header-profile__link.active::after {
  transform: rotate(180deg);
}

.header-profile__link-icon {
  display: block;
  width: 18px;
  height: 22px;
}

.header-profile__list {
  position: absolute;
  right: 0;
  top: calc(100% + 11px);
  display: none;
  background: #FFFFFF;
  border-radius: 6px;
  overflow: hidden;
  width: 210px;
  box-shadow: 0px 2px 15px rgba(7, 14, 77, 0.1);
}

.header-profile__list.active {
  display: block;
}

.header-profile__list-item + .header-profile__list-item {
  border-top: 1px solid #E3E3E3;
}

.header-profile__list-link {
  display: flex;
  align-items: center;
  padding: 12px 24px;
  font-family: 'Graphik LCG', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #2F2F2F;
}

.header-profile__list-link_enter {
  font-weight: 500;
  color: #6776FF;
}

.header-profile__list-link_enter::before {
  content: '';
  width: 16px;
  height: 16px;
  display: block;
  margin-right: 8px;
  background: url("../img/enter.svg") center center/contain no-repeat;
}

.logo {
  width: 100%;
  height: 100%;
}

.logo__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.notification__icon {
  display: block;
  width: 18px;
  height: 21px;
}

.notification__icon_new {
  position: relative;
}

.notification__icon_new::after {
  content: '';
  position: absolute;
  right: -3px;
  top: -2px;
  width: 12px;
  height: 12px;
  display: block;
  background: #6776FF;
  border: 1px solid #FFFFFF;
  border-radius: 50%;
}

.burger {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 15px;
  height: 12px;
}

.burger__item {
  width: 100%;
  height: 2px;
  display: block;
  border-radius: 4px;
  background-color: #6776FF;
  transition: .5s all linear;
}

.burger.active {
  width: 16px;
  height: 16px;
  position: relative;
}

.burger.active .burger__item {
  position: absolute;
}

.burger.active .burger__item:nth-child(2) {
  opacity: 0;
}

.burger.active .burger__item:nth-child(1) {
  left: 0;
  top: 8px;
  transform: rotate(45deg);
}

.burger.active .burger__item:nth-child(3) {
  right: 0;
  top: 8px;
  transform: rotate(-45deg);
}

@media screen and (max-width: 767px) {
  .header__nav {
    position: fixed;
    width: 100%;
    top: 64px;
    left: 0;
    z-index: 99;
    background: #FFFFFF;
    box-shadow: 0px 2px 15px rgba(7, 14, 77, 0.1);
    border-radius: 0px 0px 20px 20px;
    padding: 24px 0 40px 0;
    display: none;
    max-height: calc(100vh - 64px);
    overflow-x: hidden;
    overflow-y: auto;
  }
  .header__nav.active {
    display: block;
  }
  .header__profile-block_profile {
    display: none;
  }
  .header__profile-block_burger {
    display: block;
  }
  .header-nav {
    max-width: 295px;
    margin: 0 auto;
  }
  .header-nav__divider {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 32px 0 8px 0;
    background: #C6D1FF;
    height: 50px;
    border-radius: 20px;
  }
  .header-nav__divider-icon {
    width: 21px;
    height: 25px;
  }
  .header-nav__list {
    flex-direction: column;
  }
  .header-nav__list-mobile {
    display: flex;
  }
  .header-nav__item {
    width: 100%;
  }
  .header-nav__item + .header-nav__item {
    margin: 8px 0 0 0;
  }
  .header-nav__item + .header-nav__item_enter {
    margin: 25px 0 0 0;
  }
  .header-nav__link {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #6776FF;
    border-radius: 20px;
    height: 58px;
    width: 100%;
  }
  .header-nav__link_blue {
    background: #6776FF;
    color: #fff;
  }
  .header-nav__link_enter {
    height: auto;
    justify-content: flex-end;
    font-weight: 500;
    border: none;
    color: #6776FF;
  }
  .header-nav__link_enter::before {
    content: '';
    width: 22px;
    height: 22px;
    display: block;
    margin-right: 6px;
    background: url("../img/enter.svg") center center/contain no-repeat;
  }
  .header-nav__link::after {
    display: none;
  }
}

