.card__wrapper {
  padding: 20px 24px;
  border-radius: 16px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  height: 240px;
}

.card__wrapper:hover .card__content {
  opacity: 1;
}

.card__wrapper:hover .card__logo {
  display: block;
}

.card__wrapper:hover .card__difficult {
  display: none;
}

.card__wrapper:hover .card__bonus {
  display: none;
}

.card__header {
  display: flex;
  align-items: flex-start;
}

.card__header_center {
  align-items: center;
}

.card__header-block + .card__header-block {
  margin-left: 30px;
}

.card__header-block_time {
  flex-shrink: 0;
}

.card__title {
  font-family: 'Gilroy', sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
}

.card__time {
  font-family: 'Graphik LCG', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #6776FF;
  flex-shrink: 0;
}

.card__content {
  margin: 12px 0 auto 0;
  opacity: 0;
}

.card__content_big {
  margin: 21px 0 auto 0;
}

.card__content_visible {
  opacity: 1;
}

.card__text {
  font-family: 'Graphik LCG', sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
}

.card__text_second {
  font-family: 'Gilroy', sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
}

.card__footer {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  height: 30px;
}

.card__footer_full {
  height: 100%;
}

.card__footer-text + .card__footer-text {
  margin: 8px 0 0 0;
}

.card__logo {
  height: 24px;
  display: none;
}

.card__logo-item {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.card__img {
  width: 100px;
  height: 22px;
  object-fit: contain;
}

.bonus__wrapper {
  width: 112px;
  height: 40px;
  background: #FFA319;
  border-radius: 21px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bonus__wrapper::before {
  content: '';
  width: 33px;
  height: 33px;
  display: block;
  background: url("../img/bonuse.svg") center center/contain no-repeat;
  margin-right: 4px;
}

.bonus__text {
  font-family: 'Gilroy', sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  color: #FFFFFF;
}

.difficult__wrapper {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 36px));
  grid-auto-rows: 16px;
  gap: 2px;
  border-radius: 8px;
  overflow: hidden;
}

.difficult__item {
  height: 100%;
}

.difficult__item_low {
  background: #C6D1FF;
}

.difficult__item_medium {
  background: #A5B1FF;
}

.difficult__item_hard {
  background: #6776FF;
}

@media screen and (max-width: 767px) {
  .card__wrapper {
    padding: 20px;
  }
}
