body{
  color:#2F2F2F;
  &.no-scroll-m{
    overflow: hidden;
  }
}

.container{
  max-width: 1360px;
  width: 100%;

  padding: 0 40px;

  margin: 0 auto;
}

.icon{
  width: 100%;
  height: 100%;
}

.title{
  &_size{
    &-big{
      font-family: 'Gilroy', sans-serif;
      font-size: 44px;
      line-height: 50px;
      font-weight: 700;
    }
    &-medium{
      font-family: 'Gilroy',sans-serif;
      font-weight: 700;
      font-size: 41px;
      line-height: 51px;
    }
  }
  &_color{
    &-white{
      color:#fff;
    }
  }
  &_position{
    &-center{
      text-align: center;
    }
  }
}

.button{
  -webkit-appearance: none;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;

  height: 48px;
  width: 205px;

  font-family: 'Gilroy', sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  &-white{
    background: #FFFFFF;

    font-family: 'Gilroy',sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;

    color: #5465FF;
  }
  &-blue{
    font-family: 'Gilroy', sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;

    color: #FFFFFF;

    background: #6776FF;
  }
  &-bordered{
    border-radius: 40px;
  }
}

@media screen and (max-width: 767px) {
  .title{
    &_size{
      &-big{
        font-size: 28px;
        line-height: 30px;
      }
      &-medium{
        font-size: 28px;
        line-height: 30px;
      }
    }
  }
}